import axios from 'axios';

const customAxios = axios.create({
    baseURL: '/api'
});

customAxios.interceptors.request.use(config => {
    if(!config.headers) return config;

    const accessToken = localStorage.getItem('accessToken');
    if(accessToken) {
        config.headers["Authorization"] = `Bearer ${accessToken}`;
    }
    return config;
}, err => err);

customAxios.interceptors.response.use( res => {    
    return res;
}, async err => {
    console.log('err : ' + err.response.status)
    if(err.response.status === 401) {        
        if(localStorage.getItem('accessToken')) { //RT로 AT 재발급
            const { data } = await customAxios.get('/user/access-token');
            if(data && data.resultData) {
                localStorage.setItem('accessToken', data.resultData);
                err.config.headers['Authorization'] = `Bearer ${data.resultData}`;
            }
        }

         // 중단된 요청을(에러난 요청)을 토큰 갱신 후 재요청
        const response = await axios.request(err.config);
        return response;
    }
    return Promise.reject(err);
});

export default customAxios;