import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper/modules';
import { Link } from 'react-router-dom';
import ProfileImg from '../components/ProfileImg';
import feedAjax from '../ajax/feedAjax';
import FeedCommentList from '../components/FeedCommentList';

import { useRecoilValue } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

import styles from './FeedBox.module.css';
import 'swiper/css';

const FeedBox = ({ props, feedIndex=0, onDelFeed, ynDel }) => {    
    const userInfo = useRecoilValue(userInfoState);    
    const [ isLike, setIsLike ] = useState(props.isLike);  
    
    const getDateTimeInfo = dt => {
        const nowDt = new Date();
        const targetDt = new Date(dt);
    
        const nowDtSec = parseInt(nowDt.getTime() / 1000);
        const targetDtSec = parseInt(targetDt.getTime() / 1000);
    
        const diffSec = nowDtSec - targetDtSec;
        if(diffSec < 120) {
            return '1분 전';
        } else if(diffSec < 3600) { //분 단위 (60 * 60)
            return `${parseInt(diffSec / 60)}분 전`;
        } else if(diffSec < 86400) { //시간 단위 (60 * 60 * 24)
            return `${parseInt(diffSec / 3600)}시간 전`;
        } else if(diffSec < 2592000) { //일 단위 (60 * 60 * 24 * 30)
            return `${parseInt(diffSec / 86400)}일 전`;
        }
        return targetDt.toLocaleString();
    }

    const styleItem = `${styles.Item} mb-3 p-3 w400`;
    
    const toggleLike = async () => {
        const params = {            
            feedId: props.feedId
        }
        const { data } = await feedAjax.toggleFeedLike(params);
        if(data) {
            setIsLike(data.resultData);         
        }
    }

    const delFeed = async e => {
        if(!window.confirm('삭제하시겠습니까?')) { return; }
        const params = {
            feed_id: props.feedId           
        }
        const { data } = await feedAjax.deleteFeed(params);
        if(!data.resultData) { console.log('삭제를 실패하였습니다.'); return; }
        onDelFeed(feedIndex);
    }

    return (
        <div className={styleItem}>
            <div className='d-flex flex-row ps-3 pe-3'>
                <div className="d-flex flex-column justify-content-center">
                    <Link to={`/profile/${props.writerUserId}`}><ProfileImg userId={props.writerUserId} pic={props.writerPic} size={30} clsValue='pointer profile' /></Link>
                </div>
                <div className="p-3 flex-grow-1">
                    <div> <Link to={`/profile/${props.writerUserId}`}><span className="pointer">{props.writerNm}</span></Link>  - { getDateTimeInfo(props.createdAt) }</div>
                    <div>{props.location == null ? '' : props.location}</div>
                </div>
                {
                    ynDel && props.writerUserId == userInfo.userId &&
                    <div className="d-flex flex-column justify-content-center">
                        <i className="fa fa-trash pointer color-red" onClick={delFeed}></i>
                    </div>
                }
            </div>
            
            <Swiper
                modules={[Navigation, Pagination, Scrollbar, A11y]}
                spaceBetween={50}
                slidesPerView={1}
                navigation
                pagination={{ clickable: true }}
                scrollbar={{ draggable: true }}>

                {
                    props.pics 
                    ? 
                    props.pics.map((item, idx) => {
                                return (
                                    <SwiperSlide key={idx}>
                                        <img src={`/pic/feed/${props.feedId}/${item}`} className="w614" />
                                    </SwiperSlide>
                                );
                            })
                    : null
                }

            </Swiper>          
            <div className="favCont p-2 d-flex flex-row">
                <i className={`${isLike ? 'fas':'far'} fa-heart pointer rem1_2 me-3 color-red`} onClick={toggleLike}></i>                
            </div>             
            {
                props.contents 
                ? <div className="itemCtnt p-2">{props.contents}</div>
                : null
            }        

            <FeedCommentList feedId={props.feedId} comment={props.comment} />

            
        </div>
    );
}

export default FeedBox;