import React from 'react';
import ProfileImg from './ProfileImg';
import { useRecoilValue } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';
import feedCommentAjax from '../ajax/feedCommentAjax';


const FeedCommentBox = ({ idx, props, onDelComment }) => {    
    const userInfo = useRecoilValue(userInfoState);

    const delComment = async e => {
        if(!window.confirm('삭제하시겠습니까')) {
            return;
        }
        const params = {
            feed_comment_id: props.feedCommentId            
        }
        
        const { data } = await feedCommentAjax.deleteFeedComment(params);
        if(!data.resultData) {
            alert('삭제 실패!');
            return;
        }
        onDelComment(idx);
    }

    return (
        <div className="cmtItemCont">
            <div className="cmtItemProfile">
                <ProfileImg clasValue="profile pointer" size="24" pic={props.writerPic} userId={props.writerId} />
                {
                    userInfo.userId === props.writerUserId &&
                    <i className="fa fa-trash pointer" onClick={delComment}></i>
                }
            </div>
            <div className="cmtItemCtnt">
                <div className="pointer">{props.writerNm}</div>
                <div>{props.comment}</div>
            </div>
        </div>
    );
}

export default FeedCommentBox;