import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import userAjax from '../ajax/userAjax';
import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';

const SignInPage = () => {
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);    
    const navigate = useNavigate();
    const [ inputs, setInputs ] = useState({uid: '', upw: ''});
    
    const { uid, upw } = inputs;


    //setUserInfo({ isLogin: true, userId: 1, nm:'홍길동', pic:'' });

    const _onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    const _onKeyDown = e => {
        if(e.key === 'Enter') {
            onClickSignin();
        }
    }

    const onClickSignin = async () => {
        console.log(inputs);
        try{
            const { data } = await userAjax.postSignIn(inputs);
            if(!data.resultData) {
                alert('에러 발생!');
                return;
            }
    
            const { userId, nickName, pic, accessToken } = data.resultData;
    
            if(accessToken != null && userId > 0) {
                localStorage.setItem('accessToken', accessToken);
                setUserInfo({ isLogin: true, userId, nickName, pic });
                navigate('/feed');
            } else {
                alert(data.resultMsg);
            }       

        } catch(e) {
            alert(e.response.data.message);
        }
        
    }

    
    const host = window.location.origin;    
    const redirectUrl = `${host}/fe/redirect`; //로그인 완료 후 토큰을 받을 FE 주소 

    return (
        <section className="back_color">
            <div className="container">
                <h1>Sign in</h1>
                <div>
                    <div><label><input type="text" name="uid" placeholder="id" value={uid} onChange={_onChangeInput} /></label></div>
                    <div><label><input type="password" name="upw" placeholder="password" value={upw} onChange={_onChangeInput} onKeyDown={_onKeyDown} /></label></div>
                    <div><input type="button" id="btn_submit" value="Sign in" onClick={onClickSignin} /></div>
                    <div>
                        <Link to={'/sign-up'}>Sign up</Link>                            
                    </div>

                    <div className="m_t_20">                        
                        <div className="cursor"><a href={`/oauth2/authorization/google?redirect_uri=${redirectUrl}`}>Google</a></div>
                        <div className="cursor"><a href={`/oauth2/authorization/google?redirect_uri=${host}/fe/redirec`}>유효하지않은 Redirect Google</a></div>
                        <div className="cursor"><a href={`/oauth2/authorization/kakao?redirect_uri=${redirectUrl}`}>Kakao</a></div>
                        <div className="cursor"><a href={`/oauth2/authorization/naver?redirect_uri=${redirectUrl}`}>Naver</a></div>                        
                    </div>
                </div>                
            </div>
        </section>
    );
}

export default SignInPage;