import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import userAjax from '../ajax/userAjax';

const SignUpPage = () => {    
    const navigate = useNavigate();
    const [ paramFile, setParamFile ] = useState(null);
    const [ inputs, setInputs ] = useState({uid: '', upw: '', chkUpw: '', nickName: '', pic: ''});
    
    const { uid, upw, chkUpw, nickName } = inputs;

    const onChangeInput = e => {
        const { value, name } = e.target;
        console.log(name);
        setInputs({
            ...inputs
            , [name]: value
        });
    }
    
    const onClickSignup = async () => {     
        const { upw, chkUpw, uid, nickName } = inputs;        

        if(upw !== chkUpw) {
            alert('비밀번호를 확인해 주세요.');
            return;
        }
        
        const param = { uid, upw };

        if(nickName && nickName.length > 0) {
            param['nickName'] = nickName;
        }

        const formData = new FormData();
        formData.append('p', new Blob([JSON.stringify(param)], {type: 'application/json'}));
        if(paramFile != null) {
            formData.append('pic', paramFile);
        }
                
        const { data } = await userAjax.postSignUp(formData);
        console.log(data);
        if(data.resultData === 0) {
            alert('에러 발생!');
            return;
        }                
        navigate('/sign-in');
    }

    const onChangeFile = e => {
        if(e.target.files[0] != null) {
            setParamFile(e.target.files[0]);
        }        
    }

    return (
        <section className="back_color">
            <div className="container">
                <h1>Sign up</h1>
                <div>
                    <div><label><input type="text" name="uid" placeholder="id" value={uid} onChange={onChangeInput} /></label></div>
                    <div><label><input type="password" name="upw" placeholder="password" value={upw} onChange={onChangeInput} /></label></div>
                    <div><label><input type="password" name="chkUpw" placeholder="confirm password" value={chkUpw} onChange={onChangeInput} /></label></div>
                    <div><label><input type="text" name="nickName" placeholder="nick name" value={nickName} onChange={onChangeInput} /></label></div>
                    <div><label>프로필 사진: <input name="pic" type="file" onChange={onChangeFile} accept="image/*" /></label></div>
                    <div><input type="button" value="Sign up" onClick={onClickSignup} /></div>
                    <div>
                        <Link to={'/sign-in'}>Sign in</Link>                            
                    </div>
                </div>
            </div>
        </section>
    );
}

export default SignUpPage;