import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

import { useRecoilValue, useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';


import feedAjax from '../ajax/feedAjax';
import ProfileImg from '../components/ProfileImg';

const Header = ({setListData}) => {
    const [ picCnt, setPicCnt ] = useState(1);
    const [ inputs, setInputs ] = useState({location: '', contents: ''});
    const [ files, setFiles ] = useState([]);
    const { location, contents } = inputs;
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);
    

    const navigate = useNavigate();

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    const onChangeFile = e => {
        setFiles(Array.from(e.target.files));
    }

    const send = async () =>  {        

        const params = {            
             location: inputs.location
            , contents: inputs.contents            
        }

        const formData = new FormData();
        formData.append('p', new Blob([JSON.stringify(params)], {type: 'application/json'}));

        files.forEach(item => {
            formData.append('pics', item)
        });        
        const { data } = await feedAjax.postFeed(formData);

        if(!data.resultData) {
            alert('통신 에러!');
            return;
        }        
       
        if(setListData) {
            const item = {
                ...params                
                , feedId: data.resultData.feedId
                , pics: data.resultData.pics
                , writerId: userInfo.userId
                , writerNm: userInfo.nickName     
                , writerPic: userInfo.pic
                , createdAt: getCurrentTimestamp()      
                , comment: {
                    moreComment: false,
                    commentList: []
                }  
            }

            setListData(prev => [item, ...prev]);
        }

        for(const key in inputs) { 
            inputs[key] = '';
        }
        inputs['contents'] = '';
        setPicCnt(1);

        const $closeBtn = document.querySelector('.btn-close');
        $closeBtn.click();
        setFiles([]);
    }
    
    const getCurrentTimestamp = () => {
        const today = new Date();

        const year = today.getFullYear();
        const month = ('0' + (today.getMonth() + 1)).slice(-2);
        const day = ('0' + today.getDate()).slice(-2);

        const hours = ('0' + today.getHours()).slice(-2); 
        const minutes = ('0' + today.getMinutes()).slice(-2);
        const seconds = ('0' + today.getSeconds()).slice(-2); 

        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    }

    const procSignout = () => {
        setUserInfo({ isLogin: false, userId: null, nm: null, pic: null });
        navigate('/sign-in');
    }

    return (
        <header  className="container py-3">  
            <div id="globalConst">    
                <div  className="d-flex flex-column flex-md-row align-items-center">
                    <div  className="d-inline-flex flex-grow-1 flex-shrink-0">
                        <Link to={'/feed'}><img src="/fimg/logo.png"  className="h24 w24"/></Link>
                    </div>
                    <div  className="d-inline-flex flex-grow-1 flex-shrink-0">
                    </div>
                    <div  className="d-inline-flex flex-grow-1 flex-shrink-0">
                        <nav  className="d-flex flex-grow-1 flex-column flex-md-row justify-content-end">
                            <div  className="d-inline-flex me-3">
                                <a href="#" id="newFeedModalBtn" data-bs-toggle="modal" data-bs-target="#newFeedModal">
                                    <svg aria-label="새로운 게시물"  className="_8-yf5" color="#262626" fill="#262626" height="24" role="img" viewBox="0 0 24 24" width="24">
                                        <path d="M2 12v3.45c0 2.849.698 4.005 1.606 4.944.94.909 2.098 1.608 4.946 1.608h6.896c2.848 0 4.006-.7 4.946-1.608C21.302 19.455 22 18.3 22 15.45V8.552c0-2.849-.698-4.006-1.606-4.945C19.454 2.7 18.296 2 15.448 2H8.552c-2.848 0-4.006.699-4.946 1.607C2.698 4.547 2 5.703 2 8.552z" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round"  strokeWidth="2">
                                        </path>
                                        <line fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round"  strokeWidth="2" x1="6.545" x2="17.455" y1="12.001" y2="12.001">
                                        </line>
                                        <line fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round"  strokeWidth="2" x1="12.003" x2="12.003" y1="6.545" y2="17.455">
                                        </line>
                                    </svg>
                                </a>
                            </div>

                            <div className="d-inline-flex me-3">
                                
                            </div>

                            <div className="d-inline-flex dropdown">
                                <a href="#" role="button" id="navDropdownMenuLink" data-bs-toggle="dropdown" aria-expanded="false"  className="header_profile">
                                    <ProfileImg userId={userInfo.userId} pic={userInfo.pic} size={24} clsValue='pointer profile' />
                                </a>
                                <ul className="dropdown-menu" aria-labelledby="navDropdownMenuLink">
                                    <li>
                                        <Link to={`/profile/${userInfo.userId}`}>
                                            <span className="dropdown-item">
                                                <span>
                                                    <svg aria-label="프로필"  className="_8-yf5 " color="#262626" fill="#262626" height="16" role="img" viewBox="0 0 24 24" width="16">
                                                        <circle cx="12.004" cy="12.004" fill="none" r="10.5" stroke="currentColor" strokeLinecap="round" strokeMiterlimit="10"  strokeWidth="2"></circle><path d="M18.793 20.014a6.08 6.08 0 00-1.778-2.447 3.991 3.991 0 00-2.386-.791H9.38a3.994 3.994 0 00-2.386.791 6.09 6.09 0 00-1.779 2.447" fill="none" stroke="currentColor" strokeLinecap="round" strokeMiterlimit="10" strokeWidth="2"></path><circle cx="12.006" cy="9.718" fill="none" r="4.109" stroke="currentColor" strokeLinecap="round" strokeMiterlimit="10"  strokeWidth="2">
                                                        </circle>
                                                    </svg>
                                                </span>
                                                <span>프로필</span>   
                                            </span>
                                        </Link>
                                    </li>
                                    <li><hr className="dropdown-divider"></hr></li>
                                    <li><span className="dropdown-item" onClick={procSignout}>Sign out</span></li>
                                </ul>
                            </div>
                        </nav>
                    </div>
                </div>
            </div>    

            <div className="modal fade" id="newFeedModal" tabIndex="-1" aria-labelledby="newFeedModalLabel" aria-hidden="false">
                <div className="modal-dialog modal-dialog-centered modal-xl">
                    <div className="modal-content" id="newFeedModalContent">
                        <div className="modal-header">
                            <h5 className="modal-title" id="newFeedModalLabel">새 게시물 만들기</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body" id="id-modal-body">                            
                            <div>location: <input type="text" name="location" onChange={onChangeInput} placeholder="위치" value={location} /></div>
                            <div>contents: <textarea name="contents" onChange={onChangeInput} placeholder="내용" value={contents}></textarea></div>
                            <div><label>pic: <input name="pics" type="file" multiple onChange={onChangeFile} accept="image/*" /></label></div>
                            <div><button onClick={send}>전송</button></div>
                        </div>
                    </div>
                </div>                
            </div> 
        </header>
    );
}

export default Header;