import './ProfilePage.css';
import React, { useRef, useState, useEffect, useCallback } from 'react';
import { useParams } from "react-router-dom";
import Header from '../components/Header';
import FeedBox from '../components/FeedBox';
import userAjax from '../ajax/userAjax';
import feedAjax from '../ajax/feedAjax';
import { useRecoilState } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';
import ProfileImg from '../components/ProfileImg';



const ProfilePage = () => {
    const ITEM_PER_COUNT = 20;
    const INFINITY_SCROLL_GAP = 500;
    const inputPicRef = useRef(null);    

    const [ inputs, setInputs ] = useState({ inputPic: '' });
    const { inputPic } = inputs;

    const onChangeFile = async e => {
        if(!e.target.files[0]) { return; }
        
        const formData = new FormData();        
        formData.append('pic', e.target.files[0]);        
        
        await changeUserPic(formData);
    }

    const delUserPic = async e => {
        const formData = new FormData();
        await changeUserPic(formData);
    };

    const changeUserPic = async formData => {
        const { data } = await userAjax.patchUserPic(formData);        
        
        setUserInfo(prev => {
            const prevData = { ...prev }
            prevData.pic = data.resultData;
            return prevData;            
        });
        setProfileInfo(prev => {
            const prevData = { ...prev }
            prevData.pic = data.resultData;
            return prevData;            
        });
    }

   
    const { userId } = useParams();
    const [ profileUserId, setProfileUserId ] = useState(parseInt(userId));    
    const [ userInfo, setUserInfo ] = useRecoilState(userInfoState);        
    const [ profileInfo, setProfileInfo ] = useState({ myFeedLikeCount: 0, feedCount: 0, nickName: '', createdAt: '', pic: '' });
    const [ followState, setFollowState ] = useState(0);

    const [ isLoading, setIsLoading ] =  useState(false);
    const [ isFinish, setIsFinish ] =  useState(true);

    const [ page, setPage ] = useState(1); 
    const [ listData, setListData ] = useState([]);
    const [ error, setError ] = useState(null);

    const handleScroll = () => {       
        if(isFinish || isLoading || parseInt(window.innerHeight + window.scrollY) + INFINITY_SCROLL_GAP <= document.documentElement.offsetHeight) {
            return;
        }        
        getFeedList(profileUserId);
    };
   

    const getProfileInfo = useCallback( async () => {
        console.log('프로필 데이터 가져오기')
        const params = { profile_user_id: parseInt(userId) };

        const { data } = await userAjax.getProfileInfo(params);        
        if(data.resultData) {
            setProfileInfo(data.resultData);
            setFollowState(data.resultData.followState);
        }
    }, [setProfileInfo]);

    const getFeedList = async () => {
        if(isLoading) {
            return;
        }

        setIsLoading(true);
        setError(null);

        const params = {
            page
            , profile_user_id: profileUserId
        }

        console.log('getFeedList');
        try {
            const { data } = await feedAjax.getFeedList(params);  
            if(data.resultData) {            
                setIsFinish(data.resultData.length < ITEM_PER_COUNT);
                setListData(prevData => [...prevData, ...data.resultData]);
            } 
        } catch(e) {
            setError(error);
        } finally {
            setIsLoading(false);
        }
    }

    function onDelFeed(feedIdx) {
        console.log(feedIdx);
        setListData(prev => {
            prev.splice(feedIdx, 1);
            return [ ...prev ];
        });
    }

    const getResponse = async (followState, params) => {
        switch(followState) {
            case 0: case 2: //post               
                return await userAjax.postFollow(params);                
            default: //delete                
                return await userAjax.deleteFollow(params);            
        }
    }

    const procFollow = async e => {
        const params = {            
            to_user_id: parseInt(userId)
        }
        const { data } = await getResponse(followState, params);
        
        if(data.resultData) {            
            switch(followState) {
                case 0: case 2:
                    setFollowState(followState + 1);
                    setProfileInfo({
                        ...profileInfo
                        , follower: profileInfo.follower + 1
                    });
                    break;
                default:
                    setFollowState(followState - 1);                           
                    setProfileInfo({
                        ...profileInfo
                        , follower: profileInfo.follower - 1                    
                    });
                    break;                    
            }
        }
    }

    const getFollowStateText = state => {
        console.log(`state : ${state}`);
        switch(state) {
            case 0:
                return '팔로우';
            case 1:
            case 3:
                return '팔로우 취소';
            case 2:
                return '맞팔로우';
        }
    }

    useEffect(() => {
        const paramUserId = parseInt(userId);
        console.log(`userId값 변화!!!! : ${paramUserId}`);
        setProfileUserId(paramUserId);
        setListData([]);
        setPage(1);                
        getProfileInfo();
        setIsFinish(false);
    }, [userId]);

    useEffect(() => {
        getFeedList();          
    }, [profileUserId])

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [isLoading]);

    return (
        <section>
            <div className="container">
                <Header setListData={setListData}></Header>
                <div className="flexContainer">
                    <div>
                    <table>
                        <thead></thead>
                        <tbody>
                            <tr>
                                <td>게시물</td>
                                <td>{profileInfo.feedCount}</td>
                                <td className="pl_10">좋아요</td>
                                <td>{profileInfo.myFeedLikeCount}</td>
                                <td className="pointer follower pl_10">팔로워</td>
                                <td className="pointer follower">{profileInfo.follower}</td>
                                <td className="pointer follow pl_10">팔로우</td>
                                <td className="pointer follow">{profileInfo.following}</td>
                                {
                                    userInfo.userId !== parseInt(userId) &&
                                    <td className="pl_10">
                                        <input type="button" className="instaBtn" value={getFollowStateText(followState)} onClick={ procFollow } />      
                                    </td>
                                }
                            </tr>
                        </tbody>
                        <tfoot></tfoot>
                    </table>
                    <div>                           
                        {
                            parseInt(userId) === userInfo.userId 
                            ?
                            <div>
                                <div className="d-inline-flex" onClick={ () => inputPicRef.current.click() }>            
                                    <ProfileImg userId={profileUserId} pic={userInfo.pic} size={300} clsValue='profile pointer' />
                                </div>

                                {
                                profileInfo.pic &&
                                <div className="d-inline-flex item_container width-50">    
                                    <i className="fa fa-minus-square color-red pointer" onClick={delUserPic} />
                                </div>
                                }
                            </div>
                            :
                            <ProfileImg userId={profileUserId} pic={profileInfo.pic} size={300} clsValue='profile' />
                        }                        
                    </div>
                    <table>
                        <thead></thead>
                        <tbody>                           
                            <tr>
                                <th>name</th>
                                <td>{profileInfo.nickName}</td>
                            </tr>
                            <tr>
                                <th>reg date</th>
                                <td>{profileInfo.createdAt}</td>
                            </tr>
                        </tbody>
                        <tfoot></tfoot>
                    </table>
                    </div>
                </div>

                <div className="item_container">
                    { listData.map((item, idx) => <FeedBox key={item.feedId} props={item} userId={userInfo.userId} ynDel="1" feedIndex={idx} onDelFeed={onDelFeed} />) }
                </div>
                { isLoading && <div className="loading display-none"><img src="/fimg/loading.gif" /></div> }
                { error && <p>Error: {error.message}</p> }
            </div>

            <input type="file" ref={inputPicRef} name="inputPic" onChange={onChangeFile} accept="image/*" className="invisible" />
            
        </section>
    );
}

export default ProfilePage;