import React, { useState } from 'react';
import ProfileImg from './ProfileImg';
import feedCommentAjax from '../ajax/feedCommentAjax';
import { useRecoilState, useRecoilValue } from 'recoil';
import userInfoState from '../recoil/atoms/userInfoState';
import FeedCommentBox from '../components/FeedCommentBox';

const FeedCommentList = ({ feedId, comment }) => {    
    const userInfo = useRecoilValue(userInfoState);    
    const [ moreComment, setMoreComment ] = useState(comment.moreComment);
    const [ commentList, setCommentList ] = useState(comment.commentList ? comment.commentList : []);
    const [ inputs, setInputs ] = useState({ comment: '' });
    const { commentValue } = inputs;
    const [ isLoading, setIsLoading ] = useState(false);
    const [ page, setPage ] = useState(2); 

    const onKeyPressComment = async e => {
        if(e.key === 'Enter') {
            await postComment();
        }
    }

    const postComment = async e => {
        if(commentValue.length === 0) {
            alert('댓글을 작성해 주세요.');
            return;
        }

        const params = {
            feedId: feedId            
            , comment: commentValue
        }        

        const { data } = await feedCommentAjax.postFeedComment(params);
        if(data.resultData) {
            setInputs({
                ...inputs
                , ['commentValue']: ''
            })
            
            const commentItem = {
                feedCommentId: data.resultData
                , writerUserId: userInfo.userId
                , writerNm: userInfo.nickName
                , writerPic: userInfo.pic
                , comment: commentValue
            }
            
            setCommentList(prev => {
                return [ commentItem, ...prev ];
            });

        }   

    }

    const onChangeInput = e => {
        const { value, name } = e.target;
        setInputs({
            ...inputs
            , [name]: value
        });
    }

    const getMoreComment = async e => {
        setIsLoading(true);
        const params = {
            feed_id: feedId,
            start_idx: commentList.length
        }
        
        const { data } = await feedCommentAjax.getFeedCommentList(params);        
       
        if(data) {
            setPage(prev => prev + 1); 
            setMoreComment(data.resultData.moreComment);

            if(data.resultData.commentList.length > 0) {
                setCommentList([
                    ...commentList,
                    ...data.resultData.commentList
                ]);            
            }
        }
        setIsLoading(false);
        
    }

    function delComment(idx) {
        setCommentList(prev => {            
            prev.splice(idx, 1);
            return [ ...prev ];
        });
    }

    return(
        <div>
            <div className="overflow-y-auto max-height-240">
                {
                    commentList &&                        
                    commentList.map((item, idx) => <FeedCommentBox key={item.feedCommentId} idx={idx} props={item} onDelComment={delComment} />)                        
                }
                { isLoading && <p>Loading...</p> }
                {
                    moreComment &&                        
                    <div>
                        <span className="pointer" onClick={getMoreComment}>댓글 더보기</span>
                    </div>
                }
            </div>
            <div className="p-2 d-flex flex-row div-top">
                <input type="text" name="commentValue" className="flex-grow-1 my_input back_color" placeholder="댓글을 입력하세요..." value={commentValue} onChange={onChangeInput} onKeyUp={onKeyPressComment} />
                <button className="btn btn-outline-primary" onClick={postComment}>등록</button>
            </div>
        </div>
    )
};

export default FeedCommentList;